.container {
  width: 100%;
  height: 600px;
  background: rgba(76, 64, 169, 0.4);
}

.body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.h1 {
  font-size: 52px;
  font-family: Apple Chancery;
  color: ivory;
  text-shadow: 2px 2px rgb(201, 197, 204);
}

.anim {
  width: 80%;
  max-width: 720px;
  transform: scale(0.94);
  animation: scale 3s forwards cubic-bezier(0.5, 1, 0.89, 1);
}

@keyframes scale {
  100% {
    transform: scale(1);
  }
}

h2 {
  font-family: papyrus;
  color: rgb(9, 9, 88, 0.7);
  margin: 1px;
  padding: 15px;
}

h3 {
  font-family: papyrus;
  color: rgb(9, 9, 88, 0.7);
  margin: 1px;
  padding: 15px;
}

h4 {
  font-family: papyrus;
  font-size: 15px;
  color: rgb(9, 9, 88, 0.7);
  margin: 1px;
  padding: 15px;
}

.flower {
  display: flex;
  border: none;
  width: 50px;
  height: 50px;
  bottom: 70px;
  padding: 130px 100px 100px 80px;
  background-blend-mode: multiply;
  background-image: url(flower.png);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(76, 64, 169, 0);
  position: relative;
  font-family: papyrus;
  color: rgb(9, 9, 88, 0.7);
  font-size: 20px;
  font-weight: bold;
  animation: spin 30s linear infinite;
  -webkit-animation: spin 30s linear infinite;
  -moz-animation: spin 30s linear infinite;
  animation-name: spin;
}

.flower:hover {
  cursor: pointer;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.wing {
  display: flex;
  position: relative;
  width: 5px;
  height: 10px;
  margin-left: 500px;
  padding: 15px 15px 20px 15px;
  background-blend-mode: multiply;
  background-image: url(wing.png);
  transform: rotate(50deg);
  opacity: 0.4;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(76, 64, 169, 0);
  animation: fly linear infinite;
  animation-duration: 20s;
}

@keyframes fly {
  0%   {right:450px; bottom:250px;}
  25%  {right:300px; bottom:400px;}
  50%  {right:200px; bottom:500px;}
  75%  {right:100px; bottom:600px;}
  100% {right:0px; bottom:700px;}
}

